import React, { FC, PropsWithChildren } from 'react'
import {
  CookiesTooltip,
  CookieThemeProvider,
  ToastContainer,
  migrationThemeCookiesToCrossDomainCookiesClientSide,
} from '@lidofinance/lido-ui'

import GlobalStyles from './globalStyles'
import { Header, Main, Footer } from './layoutComponents'
import { WidgetAppProps } from './types'

// Migrations old cookies to new cross domain cookies
migrationThemeCookiesToCrossDomainCookiesClientSide()

export const WidgetApp: FC<PropsWithChildren<WidgetAppProps>> = ({ ...props }) => {
  const Providers = props.providers;
  return (
    <CookieThemeProvider>
      <GlobalStyles />
      <Providers>
        <Header
          connectedWalletInfoButton={props.connectedWalletInfoButton}
          walletConnectButton={props.walletConnectButton}
          navigation={props?.navigation}
          walletActionsLeftSlot={props?.walletActionsLeftSlot}
        />
        <Main>{props.children}</Main>
        <Footer />
        <CookiesTooltip />
        <ToastContainer />
      </Providers>
    </CookieThemeProvider>
  )
}

export { type INavigationLink } from './layoutComponents/header/components/navigation/components/navigationLink'
export { LayoutTitle, LayoutSubTitle } from './layoutComponents'
