import { FC } from 'react'
import Link from 'next/link'
import { LidoLogo } from '@lidofinance/lido-ui'

import { HeaderActions } from './components/headerActions'
import { Navigation } from './components/navigation'
import { HeaderStyle, HeaderLogoStyle } from './styles'
import { IHeader } from './types'

const Header: FC<IHeader> = (props) => {
  const { navigation, connectedWalletInfoButton, walletConnectButton, walletActionsLeftSlot } = props

  return (
    <HeaderStyle size="full" forwardedAs="header">
      <HeaderLogoStyle>
        <Link href="/" passHref legacyBehavior>
          <LidoLogo />
        </Link>
      </HeaderLogoStyle>
      <Navigation links={navigation} />
      <HeaderActions
        walletConnectButton={walletConnectButton}
        connectedWalletInfoButton={connectedWalletInfoButton}
        leftSlot={walletActionsLeftSlot}
      />
    </HeaderStyle>
  )
}

export default Header
